import React, { useEffect } from "react";
import { Table, Pagination, Navbar, Container, Nav } from "react-bootstrap";
import NavigationTop from "../../../Containers/HeaderTop/headerTop";
import "bootstrap/dist/css/bootstrap.min.css";
import "./branchTable.css";
import { useDispatch, useSelector } from "react-redux";
import { getAllBranches } from "../../../features/branch/branchSlice";
import { Link } from "react-router-dom";

function BranchTable() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllBranches());
  }, []);

  const { Branches } = useSelector((state) => state.branch);

  return (
    <main className="main_clientTable">
      <NavigationTop />

      <Navv />

      <div className="cennt mt-5">
        <div className="clienttable_feild  w-100 mx-4 p-2">
          <div className="sec des">
            <div className="head d-flex align-items-center justify-content-between">
              <div>
                <label> Branches </label>
              </div>
              <div>
                <AdvancedExample />
              </div>
            </div>
            <div className="body">
              <div className="body_inner py-1 ">
                <Table responsive>
                  <thead>
                    <tr className="bg-light">
                      <th>Branch Name</th>
                      <th>Branch Address</th>
                      <th>Branch Address 2</th>
                      <th>Zip</th>
                      <th>Phone</th>
                      <th>Fax</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Branches && (
                      <>
                        {Branches.map((data, i) => (
                          <tr key={i}>
                            <td>{data.allBranches_branch_name}</td>
                            <td>{data.allBranches_address_1}</td>
                            <td>{data.allBranches_address_2}</td>
                            <td>{data.allBranches_Zip}</td>
                            <td>{data.allBranches_Phone}</td>
                            <td>{data.allBranches_Fax}</td>
                          </tr>
                        ))}
                      </>
                    )}
                  </tbody>
                </Table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}

function AdvancedExample() {
  return (
    <Pagination>
      <Pagination.Item active>{1}</Pagination.Item>
      {/* <Pagination.Ellipsis /> */}

      <Pagination.Item>{2}</Pagination.Item>
      <Pagination.Item>{3}</Pagination.Item>
      <Pagination.Item>{4}</Pagination.Item>
      <Pagination.Item>{5}</Pagination.Item>
      {/* <Pagination.Item disabled>{14}</Pagination.Item> */}
      <Pagination.First />
      <Pagination.Prev />
      <Pagination.Next />
      <Pagination.Last />
    </Pagination>
  );
}

function Navv() {
  return (
    <Navbar expand="lg" className="navbar navv">
      <Container fluid className="mx-3">
        <Nav className=" my-2 my-lg-0 d-flex align-items-center " navbarScroll>
          <Link to="/allBranches" className="py-1 px-3">
            <i className="fa-solid fa-square-plus"></i> New
          </Link>
          <Link to="/branchTable" className="py-1 px-3">
            <i className="fa-solid fa-pen-to-square"></i> Edit
          </Link>
        </Nav>
      </Container>
    </Navbar>
  );
}

export default BranchTable;
